<template>
  <div id="app">
    <router-view
      v-if="$store.state.fileLoaded"
      :key="`editor-${$store.state.activeId}`"
    ></router-view>
  </div>
</template>

<script>
export default {
  name: "App",
  components: {},
  mounted() {
    let queryData = this.$route.query.d;
    if (queryData) {
      // Render single file.
      this.$store.dispatch("loadSingleFile", queryData);
    } else {
      // Load warblerFiles from localstorage.
      // Add new key query trigger.
      if (this.$route.query.key) {
        this.$store.dispatch("setKey", this.$route.query.key);
      }
      this.$store.dispatch("loadDefaultKey");
    }
  },
};
</script>

