import Vue from "vue";
import Vuex from "vuex";
import localforage from "localforage";
import sortBy from "lodash/sortBy";
import debounce from "lodash/debounce";

import { compressToEncodedURIComponent } from "@kalkih/lz-string";
import { decompressFromEncodedURIComponent } from "@kalkih/lz-string";

import { returnRandomKey } from "@/utils/randomKey";

Vue.use(Vuex);

const STORE_NAME = "CodeWarbler-Tone.js";
const NEST_STORE_NAME = "CodeWarbler-Nest";
const NEST_TONEJS_ACTIVE_KEY = "NEST_TONEJS_ACTIVE_KEY";

const defaultCode = `// Libraries available: Tone.js 14.7.77, Tonal.js 4.6.5, Lodash 4.17.21
var t = new Tone.AMOscillator(400);
t.volume.value = -12;
t.start();
t.toDestination();`;

const deboucedHistoryReplaceStateDelayMs = 1000;
const deboucedHistoryReplaceState = debounce(function (href) {
  console.log("deboucedHistoryReplaceState", href);
  window.history.replaceState({}, null, href);
}, deboucedHistoryReplaceStateDelayMs);

export default new Vuex.Store({
  state: {
    warblerFiles: [],
    forage: null,
    fileLoaded: false,
    activeId: null,
    queryFile: {},
    isQueryFile: false,
    browserFile: {},
  },
  mutations: {
    setActiveId(state, value) {
      state.activeId = value;
    },
  },
  getters: {
    activeFile(state) {
      if (state.isQueryFile) {
        return state.queryFile;
      }
      return state.browserFile;
    },
  },
  actions: {
    setKey({ dispatch, commit }, key) {
      commit("setActiveId", key);
      let nest = localforage.createInstance({
        name: NEST_STORE_NAME,
      });
      return nest
        .setItem(NEST_TONEJS_ACTIVE_KEY, key)
        .then((value) => {
          console.log("Saved!", value);
          return dispatch("loadFileFromBrowser");
        })
        .catch(function (err) {
          // This code runs if there were any errors
          console.error(err);
        });
    },
    loadDefaultKey({ commit, dispatch }) {
      let nest = localforage.createInstance({
        name: NEST_STORE_NAME,
      });
      return nest
        .getItem(NEST_TONEJS_ACTIVE_KEY)
        .then((value) => {
          console.log("loadDefaultKey!", value);
          if (!value) {
            value = returnRandomKey(11);
            dispatch("setKey", value);
          }
          commit("setActiveId", value);
          return dispatch("loadFileFromBrowser");
        })
        .catch(function (err) {
          // This code runs if there were any errors
          console.error(err);
        });
    },
    loadFileFromBrowser({ state, dispatch }) {
      if (!state.forage) {
        state.forage = localforage.createInstance({
          name: STORE_NAME,
        });
      }
      return state.forage.getItem(state.activeId).then((value) => {
        if (!value) {
          value = {
            id: state.activeId,
            data: {
              code: defaultCode,
              title: "Untitled file",
              datetime: Date.now(),
            },
          };
        } else {
          value = {
            id: state.activeId,
            data: value,
          };
        }
        console.log("value", value);
        state.browserFile = value;
        dispatch("updatePageTitle");
        state.fileLoaded = true;
      });
    },
    loadSingleFile({ state, dispatch }, queryData) {
      let decompressedData = JSON.parse(
        decompressFromEncodedURIComponent(queryData)
      );
      console.log(decompressedData);
      state.queryFile = decompressedData;
      state.isQueryFile = true;
      state.fileLoaded = true;
      dispatch("updatePageTitle");
    },
    loadFilesFromForage({ state, dispatch }) {
      console.log("loadFilesFromForage");
      // Check to see if there's a localforage instance.
      if (!state.forage) {
        state.forage = localforage.createInstance({
          name: STORE_NAME,
        });
      }
      let warblerFiles = [];
      return state.forage
        .iterate(function (value, key) {
          // Resulting key/value pair -- this callback
          // will be executed for every item in the
          // database.
          warblerFiles.push({ id: key, data: value });
        })
        .then(function () {
          state.warblerFiles = sortBy(warblerFiles, (s) => -s.data.datetime);
          console.log(state.warblerFiles);
          if (state.warblerFiles.length == 0) {
            // Create default code and load that one.
            state.activeId = returnRandomKey(11);
            let newData = {
              code: defaultCode,
              title: "Untitled file",
              datetime: Date.now(),
            };
            state.warblerFiles = [{ id: state.activeId, data: newData }];
            dispatch("saveFile", newData);
          } else {
            state.activeId = state.warblerFiles[0].id;
          }
          dispatch("updatePageTitle");
          state.fileLoaded = true;
        })
        .catch(function (err) {
          // This code runs if there were any errors
          console.log(err);
        });
    },
    updateCode({ getters, dispatch }, codeText) {
      console.log("updateCode");
      let item = getters.activeFile;
      item.data.code = codeText;
      dispatch("saveFile", item.data);
    },
    updateTitle({ getters, dispatch }, title) {
      console.log("updateTitle");
      let item = getters.activeFile;
      item.data.title = title;
      dispatch("saveFile", item.data);
    },
    saveFile({ state, getters, dispatch }, data) {
      console.log("saveFile");
      console.log(data);
      data.datetime = Date.now();
      // Migrate .name to .title here (safe to remove this after March 2022)
      if (!data.title) {
        data.title = data.name;
      }
      delete data.name;
      delete data.snippet; // Migrate .snippet (safe to remove this after March 2022)
      data.libraryName = "Tone.js";
      data.libraryVersion = process.env.VUE_APP_TONE_VERSION;
      dispatch("updatePageTitle");
      if (state.isQueryFile) {
        // Query-based warblerFiles
        let jsonString = JSON.stringify(getters.activeFile);
        let compressed = compressToEncodedURIComponent(jsonString);
        const href = `${window.location.pathname}?d=${compressed}`;
        // Debounce replacing the URL state.
        deboucedHistoryReplaceState(href);
      } else {
        // Local warblerFiles.
        console.log("saveFile - actual", data);
        state.forage
          .setItem(`${state.activeId}`, data)
          .then((value) => {
            console.log("Saved!", value);
          })
          .catch(function (err) {
            // This code runs if there were any errors
            console.error(err);
          });
      }
    },
    gotoPermanentUrl({ getters }) {
      let jsonString = JSON.stringify(getters.activeFile);
      let compressed = compressToEncodedURIComponent(jsonString);
      const href = `https://codewarbler.github.io/warbler/${process.env.VUE_APP_WARBLER_VERSION}/?d=${compressed}`;
      window.open(href, "_blank");
    },
    updatePageTitle({ getters }) {
      console.log(getters.activeFile);
      document.title = `${getters.activeFile.data.title} - CodeWarbler`;
    },
  },
});
