<template>
  <div class="header">
    <div class="left">
      <h1>Warbler</h1>
    </div>

    <div class="center large-only">
      <div>
        <span>
          <button @click="startFunction">Play</button>
          <button @click="closeContext">Stop</button>
        </span>
      </div>
    </div>

    <div class="right large-only">
      <label for="title">Title: </label>
      <input id="title" v-model="title" />
      <button @click="$store.dispatch('gotoPermanentUrl')">
        Permanent Link
      </button>
    </div>

    <div class="center small-only">
      <span>
        <button @click="startFunction">Play</button>
        <button @click="closeContext">Stop</button>
      </span>
      <label for="title">Title: </label>
      <input id="title" v-model="title" />
      <button @click="$store.dispatch('gotoPermanentUrl')">
        Permanent Link
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "Header",
  data() {
    return {
      version: process.env.VUE_APP_WARBLER_VERSION,
    };
  },
  computed: {
    title: {
      get() {
        return this.$store.getters.activeFile.data.title;
      },
      set(value) {
        this.$store.dispatch("updateTitle", value);
      },
    },
  },
  methods: {
    startFunction() {
      this.$emit("startFunction");
    },
    closeContext() {
      this.$emit("closeContext");
    },
  },
};
</script>

